import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import { Button } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import DeleteIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles((theme) => ({
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    backgroundColor: '#444',
    height: '3rem',
  },
  content: {
    marginLeft: '2.7rem',
    marginRight: '3.2rem',
    alignItems: 'center',
    display: 'flex',
  },
  total: {
    alignItems: 'center',
    marginTop: '0.4rem',
    color: 'white',
  },
  icon: {
    color: '#C5C5C5',
    '&:hover': {
      color: 'white',
    }
  },
  productos: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
  },
  producto: {
    padding: '40px',
  },
  paper: {
    padding: theme.spacing(2),
    marginLeft: '3.5rem',
    maxWidth: 400,
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  agregar: {
    backgroundColor: '#FFD900',
    '&:hover': {
      backgroundColor: '#D8B800',
    }
  },
}))

export default function Cart({ cart, clearCart, removePromoCart, removeBurgerCart, removePapasCart, removeBebidasCart }){
  const classes = useStyles(); 

  const getTotalSum = () => {
    return cart.reduce(
      (sum, { precio }) => sum + precio,
      0
    );
  };

  const productsWhatsapp = cart.map(
    (product) =>
          product.nombre + "%20" + "$" + product.precio
  );

  console.log(productsWhatsapp);

  function getLinkWhastapp() {
    var url =
      "https://api.whatsapp.com/send?phone=" +
      "541121936289" +
      "&text=" +
      encodeURIComponent("Mi pedido es: ") +
      "%0a%0a" +
      productsWhatsapp +
      "%0a" +
      encodeURIComponent("El total es: $" + getTotalSum());

    return window.open(url);
  }

  return (
    <>
      <div className={classes.header}>
        <div className={classes.content}>
          <IconButton onClick={clearCart}>
            <DeleteIcon fontSize="large" className={classes.icon} />
          </IconButton>
          <Typography className={classes.total} variant="body1" gutterBottom>
            Eliminar Todo
          </Typography>
        </div>

        <div className={classes.content}>
          <Typography className={classes.total} variant="h6" gutterBottom>
            Total: ${getTotalSum()}
          </Typography>
        </div>

        <div className={classes.content}>
          <Typography className={classes.total} variant="body1" gutterBottom>
            Enviar mi pedido: 
          </Typography>
          <IconButton onClick={() => getLinkWhastapp()}>
            <WhatsAppIcon fontSize="large" className={classes.icon} />
          </IconButton>
        </div>
      </div>
      <div className={classes.productos}>
      {cart.map((producto, index) => (
        <div className={classes.producto} key={index}>
      <Paper className={classes.paper}>
        <Grid container spacing={2}>
          <Grid item>
            <ButtonBase className={classes.image}>
            <img 
              className= {classes.img} 
              alt= {producto.nombre} 
              src= {producto.img} 
            />
            </ButtonBase>
          </Grid>
          <Grid item xs={12} sm container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography gutterBottom variant="subtitle1">
                  {producto.nombre}
                </Typography>
                <Typography variant="body2" gutterBottom>
                  {producto.descripcion}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  ID: 1030114
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="body2" style={{ cursor: 'pointer' }}>
                <Button 
                  onClick={() => removePromoCart(producto)}
                  className={classes.agregar} 
                  variant="outlined"
                  >
                  Eliminar
                </Button>
                </Typography>
              </Grid>
            </Grid>
            <Grid item>
              <Typography variant="subtitle1">${producto.precio}</Typography>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
      </div> 
    ))}
    </div>
    </>
  )
}


