import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles ({
    boton: {
        marginTop: '0.5rem',
        marginBottom: '0.5rem',
        // marginLeft: '55.7rem',
        borderRadius: '50px',
        backgroundColor: '#DB8300 ',
        textTransform: 'none',
        color: 'white',
        '&:hover': {
            backgroundColor: '#C77803',
          },
    },
});

export default function ResponsiveDialog() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button variant="outlined" className={classes.boton} onClick={handleClickOpen}>
        Ayuda
      </Button>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Recomendaciones"}</DialogTitle>
        <DialogContent>
          <DialogContentText>
            1. Para poder navegar por las categorías, deberás registrarte e iniciar sesión.
          </DialogContentText>
          <DialogContentText>
            2. El asistente de compra será tu guía, tanto en categorías como en tu carrito.
          </DialogContentText>
          <DialogContentText>
            3. Cada comida/bebida tiene su carta, cuando elijas deberás hacerlo con el botón "Agregar".
          </DialogContentText>
          <DialogContentText>
            4. En el carrito verás todas las cartas que has agregado. Además, podrás eliminar alguna carta en caso de que ya no la quieras, o todas las cartas con el ícono de eliminar.
          </DialogContentText>
          <DialogContentText>
            5. Verás el cartel con el total del pedido, que se actualizará a medida que agregues o elimines cartas.
          </DialogContentText>
          <DialogContentText>
            6. Para finalizar la compra, haz click en el botón de 'WhatsApp', donde hará una redirección a la aplicación y podrás hacer el pedido de lo seleccionado. 
          </DialogContentText>
          <DialogContentText>
            EXTRA: "Ayudanos a mejorar", En la sección "Más" del menú, podrás dejarnos tu experiencia con la app.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" autoFocus>
            Cerrar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
