import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import {AuthProvider} from './components/Auth';
import PrivateRoute from './components/PrivateRoute';
import Header from './components/Header';
import Inicio from './components/Inicio';
import Productos from './components/Productos';
import Menu from './components/Menu';
import Nosotros from './components/Nosotros';
import Mas from './components/Mas';
import Registrarse from './components/Registrar';
import Login from './components/Login';
import Hamburguesas from './components/Hamburguesas';
import Papas from './components/Papas';
import Bebidas from './components/Bebidas';
import Cart from './components/Cart';
import Ayuda from './components/Ayuda'
import PageNotFound from './components/PageNotFound';

function App() {
  return (
    <AuthProvider>
      <Router>
      <div className="app">
        <Header/>
        <Switch>
          <Route exact path="/comida-facil/" component={Inicio}/>
          <Route exact path="/comida-facil/menu" component={Menu}/>
          <Route exact path="/comida-facil/nosotros" component={Nosotros}/>
          <Route exact path="/comida-facil/mas" component={Mas}/>
          <Route exact path="/comida-facil/registrate" component={Registrarse}/>
          <Route exact path="/comida-facil/iniciar-sesion" component={Login}/>

          <PrivateRoute exact path="/comida-facil/productos" component={Productos}/>
          <Route exact path="/comida-facil/hamburguesas" component={Hamburguesas}/>
          <Route exact path="/comida-facil/papas" component={Papas}/>
          <Route exact path="/comida-facil/bebidas" component={Bebidas}/>
          <Route exact path="/comida-facil/cart" component={Cart}/>

          <Route exact path="/comida-facil/menu" >
            <Productos/>
            <Hamburguesas/>
            <Papas/>
            <Bebidas/>
          </Route>

          {/* <Route exact path="/comida-facil/ayuda" component={Ayuda} />  */}

          <Route exact component={PageNotFound}/>
        </Switch>
      </div>
      </Router>
    </AuthProvider>
  );
}

export default App;
