import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import AsistenteCompra from './AsistenteCompra';
import CardMenu from './CardMenu';
import Footer from './Footer';
import Alert from '@material-ui/lab/Alert';

const useStyles = makeStyles ({
    subtitle: {
        paddingTop: '7.9rem',
        display: 'flex',
        justifyContent: 'center',
    },
    info: {
        marginTop: '1.2rem',
        marginBottom: '1.2rem',
        display: 'flex',
        justifyContent: 'center',
        marginLeft: '22.9rem',
        marginRight: '22.7rem',
    },
    section: {
        paddingTop: '0.9rem',
    },
})

const Menu = () => {
    const classes = useStyles();

    return (
            <div>
                    <AsistenteCompra />
                    <Typography className={classes.subtitle} variant="h6" gutterBottom>
                        Éstas son las categorías disponibles:
                    </Typography>
                    <div className={classes.section}>
                        <CardMenu/>
                    </div>
                    <Alert severity="info" className={classes.info}>Puedes agregar al carrito e ir a otra categoría, no se perderá lo agregado.</Alert>
                    <Footer />

            </div>
    );
}

export default Menu;

